.square {
    position: relative;
    width: 250px;
    height: 250px;
    background: #FFFFFF;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    border-radius: 15px;
    // z-index: 2;


}

.square:hover .square-content-pillars-inner{
    transform: rotateY(180deg);

}

// .square:before {
//     content: "";
//     display: block;
//     padding-top: 20%;
//     padding-left: 50%;
// }

.square-content-pillars, .square-content-pillars-back{
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
}

.square-content-pillars-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
  }

  .square-content-pillars-back{
    transform: rotateY(180deg);
    vertical-align: middle;
    margin-top: 40px;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 10px;
    
    display: table-cell;
    position: absolute;
  }

.square-content-pillars {
    display: table-cell;
    position: absolute;
    text-align: center;
    vertical-align: middle;
    margin-top: 40px;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 10px;
    // padding-left: 40%;
    // color: white

    img {
        width: 45%;
    }

    p {
        margin-top: 20px;

        font-family: $font-nunito;
        color: #5B5B5B;
        ;
    }
}




