.square-box {
    position: relative;
    width: 100%;
    // height: 50%;
    overflow: hidden;
    background: #2BAF57;
    border-radius: 7px;
}

.square-box:before {
    content: "";
    display: block;
    padding-top: 20%;
    padding-left: 100%;
}

.square-content {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    color: white;
}

.square-content div {
    display: table;
    width: 100%;
    height: 100%;
}

.square-content span {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    color: white
}

