#contact {
    .container {
        position: relative;
        width: 100%;
        // height: 50%;
        overflow: hidden;
        background: #F0F0F0;
        border-radius: 7px;
    }

    .block {
        display: block;
        width: 90%;
        margin: auto;
        border: none;
        background-color: #79B925;
        color: white;
        padding: 5px 4px;
        font-size: 16px;
        cursor: pointer;
        text-align: center;
        border-radius: 5px;
    }

    .block:hover {
        background-color: #ddd;
        color: black;
    }

}





